import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';

@inject('tableStore')
@observer
class Table extends Component {

  fileList = props => {
    let list = props.map( (item,index) => {
      return <tr key={index}>
        <td>{item.name}</td>
        <td>{item.length}</td>
        <td>{item.size.toFixed(1)} </td>
        <td><a href={item.json} className="btn" download="result.json"></a></td>
      </tr>

    })
    return list
  }

  render() {
    return (
      <div className="form__table">
        <table>
          <thead>
          <tr>
            <th> </th>
            <th>-   json</th>
            <th>  json</th>
            <th></th>
          </tr>
          </thead>
          <tbody>
            {this.fileList(this.props.tableStore.fileArr)}
          </tbody>
        </table>
      </div>
    );
  }
}

export default Table;


